.react-photo-gallery--gallery img {
	width: 250px;
	height: 250px;

}

.react-photo-gallery--gallery {
	display: flex;
	justify-content: center;
}

.img__margin img {
	margin-left: 15px !important;
	margin-bottom: 15px !important;
	border: 3px solid #4a2c79;
}
.link_to__portfolio {
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-items: center;
	text-transform: uppercase;

}
.link_to__portfolio {
	display: none;
}
.portfolio__icon {
	font-size: 100px;

color: #976bc1;
}
.button_portfolio{

	background-color: #4b2a7a;
	border: none;

}
.button_portfolio:hover{
	background-color: #9165bc;

}
#portfolio_btn__link {
	text-transform: uppercase;
	text-decoration: none;
	color: white;
}
#portfolio_btn__link:hover{
	color: white;
}



@media screen and (min-width: 290px) and (max-width: 799px) {
	.board_box{
		display: flex;


	}

	.img__margin img {
		margin: 20px 0px 15px 0px !important;

	}
	.img__margin{
		display: flex;
		justify-content: center;
	}
.react-photo-gallery--gallery {
	width: 60% !important;
}
	.blog__title {
		display: flex;
		width: 100%;
		justify-content: center;
		height: 50px;

	}


	.button_portfolio {
		display: none;
	}
	.link_to__portfolio{
		display: flex;
	}

}
