.board_box {
	display: flex;
	justify-content: center;
	width: 100%;
	margin-top: 30px;
	margin-bottom: 15px;

}

.board_content {
	display: flex;
	justify-content: center;
	max-width: 1070px;
}



@media screen and (min-width: 290px) and (max-width: 799px) {


	.blog__title {
		width: 100%;
		height: 100%;
		text-align: center;
	}
	.blog_title__box{
		margin: 20px 0px 0px 0px;
	}
	.blog__title h1 {
		margin-top: 0px;
		color: #4a2c79;
		height: 100%;
		font-size: 25px;
		padding-left: 10px;
		padding-right: 10px;
		text-align: center;
		align-items: center;
		align-content: center;
		    text-transform: uppercase;
	}
	.board_box {

    margin-top: 0px;
    margin-bottom: 0px;
}

}
