.service_elements__box {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	max-width: 1200px;
}

.background_box a {
	width: 100%;
	height: 250px;

}


.service_card {
	margin:10px;
}
.service_card_image {
	height:200px;
	width: 250px;
	z-index: 1;
	/*background-color: #4a2c79;*/
	position: relative;

}


@media (max-width: 576px) {
.service_card_image {
		height: 100px;
		width: 150px;
}
.background_box p {
	padding-left: 15px !important;
	width: 150px !important;
	font-size: 14px!important;
}
#_box__text7 {
	padding: 10px 10px 10px 15px !important;
}
}

.service_card_image:hover {
	border: 2px solid #4a2c79;

}
.service_card_image:hover::after {
	opacity: 0;
	transition: 0.3s;

}

.service_card_image::after {
	position: absolute;
	content: '';
	opacity: 0.7;
	height: 100%;
	width: 100%;
	z-index: 3;
	background-color: #4a2c79;
	cursor: pointer;
}


.background_box p {
	text-align: left;
	position: absolute;
	bottom: 0;
	font-size: 20px;

	text-transform: uppercase;

height: 50px;
width: 250px;
	color			: white;
	font-family: 'Calibri Regular';
	letter-spacing		: -1px;
	line-height: 1;
	z-index: 15;

	padding: 10px 10px 10px 20px;
}

.AllService p {
	width: 250px !important;
}

.background_box p::after{
	position: absolute;
	bottom: 0;
	font-family: 'Calibri Regular';
	color			: white;
	letter-spacing		: -1px;
	line-height: 1;
	z-index: 10;
	padding: 10px;
}


.background_box p::before {
	content: '';
	font-family: 'Calibri Regular';
	width: 5px;
	height: 25px;
	background-color: white;
	position: absolute;
	left: 5px;
	margin-right: 10px;
	top: 10px;
}



